/* DEFAULT */

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "Inter", sans-serif;
  font-family: "Karla", sans-serif;
  font-size: 16px;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

body {
  background-color: #0b2434;
  width: 100%;
  min-height: 100vh;
 display: grid;
 place-items: center;
}

/* START NEW PROJECTS */

.main {
  max-width: 80%;
  width: 400px;
  height: 450px;
  margin: 0 auto;
  background-color: #f5f5f5;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 33px;
}

.main--title {
  font-weight: bold;
  font-size: 26px;
  line-height: 30px;
  letter-spacing: -0.03em;
  color: #2b283a;
  text-align: center;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.main--paragraph {
  font-family: Inter;
  font-size: 13px;
  line-height: 16px;
  text-align: center;
  letter-spacing: -0.03em;
  color: #4a4e74;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  margin: 10px;
}

.main--stats {
  display: grid;
  grid-template: repeat(1, 1fr) / repeat(4, 1fr);
  grid-gap: 12px;
  margin-bottom: 10px;
}

.main--stats > div {
  text-align: center;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
  border-radius: 3.81277px;
  grid-column: span 2;
  padding: 6px 4px;
  font-weight: bold;
}

.dice-container {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 20px;
  margin-top: 10px;
}

.die {
  justify-self: center;
  /* FONT */
  font-family: Karla;
  font-style: normal;
  font-weight: bold;
  font-size: 20.5889px;
  line-height: 24px;
  /* FLEX */
  /* display: flex;
  justify-content:space-around;
  align-items: center; */
  display: grid;
  /* SIZE */
  width: 40px;
  height: 40px;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
  border-radius: 3.81277px;
  cursor: pointer;
  padding: 3px;
}

.dot {
  min-width: 8px;
  min-height: 8px;
  background-color: black;
  border-radius: 50%;
  align-self: center;
  justify-self: center;
}

.die-1 {
  align-content: center;
}

.die-2,
.die-4 {
  grid-template: repeat(2, 1fr) / repeat(2, 1fr);
  justify-content: center;
}

.die-2 .dot:last-child {
  grid-row: 2;
  grid-column: 2;
}

.die-3,
.die-5 {
  grid-template: repeat(3, 1fr) / repeat(3, 1fr);
}

.die-6 {
  grid-template: repeat(3, 1fr) / repeat(2, 1fr);
}

.die-3 .dot:nth-child(2) {
  grid-row: 2;
  grid-column: 2;
}

.die-3 .dot:nth-child(3) {
  grid-row: 3;
  grid-column: 3;
}

.die-5 .dot:nth-child(2) {
  grid-row: 1;
  grid-column: 3;
}

.die-5 .dot:nth-child(3) {
  grid-row: 2;
  grid-column: 2;
}
.die-5 .dot:nth-child(4) {
  grid-row: 3;
  grid-column: 1;
}
.die-5 .dot:nth-child(5) {
  grid-row: 3;
  grid-column: 3;
}

.btn {
  align-self: center;
  margin-top: 30px;
  height: 40px;
  width: 100px;
  font-family: Karla;
  font-style: normal;
  font-weight: bold;
  font-size: 16.384px;
  line-height: 19px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: #5035ff;
  border: none;
  color: #ffffff;
  box-shadow: 0px 3.2px 7.68px rgba(0, 0, 0, 0.18);
  border-radius: 4px;
  cursor: pointer;
}

.btn:active {
  box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}
